footer {
    width: 100%;
    box-sizing: container-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 120;
}

footer p {
    color: rgb(221, 221, 221);
    font-family: 'Inter', sans-serif;
    margin-bottom: 0.8rem;
    font-size: 0.95rem;
}

.footer-p-title {
    font-size: 1.05em;
    margin-bottom: 1rem;
    font-weight: 600;
    color: white;
}

#footer-first-div-last-p {
    margin-top: 2rem;
    width: 100%;
}

#footer-logo {
    width: 4rem;
}

#footer-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: container-box;
    width: 100%;
    max-width: 1100px;
    z-index: 100;
    padding: 4rem;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(5px);
    border-top-right-radius: 2rem;
    border-top-left-radius: 2rem;
}

#footer-container div {
    width: 18%;
}

#footer-container div:first-child {
    width: 50%;
    min-width: 300px;
}

@media(max-width:750px) {
    .footer-container {
        padding: 1rem;
    }
    #footer-container div {
        width: 48%;
        margin-top: 2rem;
    }
    #footer-container div:first-child {
        width: 100%;
    }
}