/* arrow */

.wrapper {
    padding-bottom: 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
}

.arrow-div {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.arrow-div svg {
    width: 50px;
    height: 60px;
    margin: 0 1rem;
    cursor: pointer;
    overflow: visible;
    vertical-align: middle;
}

.arrow-div svg polygon,
.arrow-div svg path {
    transition: all 0.5s cubic-bezier(0.2, 1, 0.3, 1);
    fill: white;
}

.arrow-div svg:hover polygon,
.arrow-div svg:hover path {
    transition: all 1s cubic-bezier(0.2, 1, 0.3, 1);
    fill: #159e3c93;
}

.arrow-div svg:hover .arrow {
    -webkit-animation: arrow-anim 2.5s cubic-bezier(0.2, 1, 0.3, 1) infinite;
            animation: arrow-anim 2.5s cubic-bezier(0.2, 1, 0.3, 1) infinite;
}

.arrow-div svg:hover .arrow-fixed {
    -webkit-animation: arrow-fixed-anim 2.5s cubic-bezier(0.2, 1, 0.3, 1) infinite;
            animation: arrow-fixed-anim 2.5s cubic-bezier(0.2, 1, 0.3, 1) infinite;
}

@-webkit-keyframes arrow-anim {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    5% {
        -webkit-transform: translateX(-0.1rem);
                transform: translateX(-0.1rem);
    }
    100% {
        -webkit-transform: translateX(1rem);
                transform: translateX(1rem);
        opacity: 0;
    }
}

@keyframes arrow-anim {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    5% {
        -webkit-transform: translateX(-0.1rem);
                transform: translateX(-0.1rem);
    }
    100% {
        -webkit-transform: translateX(1rem);
                transform: translateX(1rem);
        opacity: 0;
    }
}

@-webkit-keyframes arrow-fixed-anim {
    5% {
        opacity: 0;
    }
    20% {
        opacity: 0.4;
    }
    100% {
        opacity: 1;
    }
}

@keyframes arrow-fixed-anim {
    5% {
        opacity: 0;
    }
    20% {
        opacity: 0.4;
    }
    100% {
        opacity: 1;
    }
}

